















































import Vue from "vue";
export default Vue.extend({
  name: "VInlineTimePicker",
  data: () => ({
    value: null as any,
    menu2: false,
    modal2: false,

  }),
  props: {
    allowedHours: {
      type: Array,
    },
    allowedMinutes: {
      type: Array,
    },
    dense: {
      type: Boolean,
    },
    disabled: {
      type: Boolean,
    },
    label: {
      type: String,
      default: "Pick a time"
    },
    min: {
      type: String,
    },
    max: {
      type: String,
    },
    outlined: {
      type: Boolean
    },
    readonly: {
      type: Boolean,
    },
    rules: {
      type: Array
    },
    value: {
      type: String,
    },
    textFieldClass: {
      type: String,
      default: ""
    },
  },

  watch: {
    value() {
      this.$emit("change");
    }
  },
destroyed() {
      this.$el?.remove();
   },
  methods: {
    timeChange() {
      this.$emit("change");
    },
    resetValidation() {
      (this.$refs.text as any).resetValidation();
    },
    validationRefs() {
      return [this.$refs.text];
    },
    clickHour(hour) {
      this.$emit("click:hour", hour);
    }
  }

});
// removed CSS here. InlineTimePicker inside a single row used to cause problem. Now col is required inside a row. so removed CSS.
